import React from 'react'
import './style-servicos.css'
import iabw from './assets/iabw.svg'
import iacolor from './assets/iacolor.svg'
import frontbw from './assets/frontbw.svg'
import frontcolor from './assets/frontcolor.svg'


export default function Servicos(){

    return(
        <div className="container-servicos">
            <div className="titulo">
                <h2>MEUS SERVIÇOS</h2>
            </div>
            <div className="div-class-servico">
                <div className="class-servico">
                    <div className="servico-1">
                        <div className="imagem">
                            <img className="img-servico" src={frontbw} id="imgbw"></img>
                            <span><img className="img-servico" src={frontcolor}></img></span>
                        </div>
                        <div className="texto">Desenvolvedor Front-End.</div>
                        <p>Em Constante Aperfeiçoamento.</p>
                    </div>
                    <div className="servico-2">
                        <div className="imagem">
                            <img className="img-servico" src={iabw} id="imgbw"></img>
                            <span><img className="img-servico" src={iacolor}></img></span>
                        </div>
                        <div className="texto">Cientista de Dados</div>
                        <p>Em Inicio de Jornada.</p>
                    </div>
                </div>
            </div>
        </div>
    )
}