import React from 'react'
import './style-contatos.css'
import FaceC from './assets/FaceColor.svg'
import FaceBW from './assets/FaceBW.svg'
import GithubBW from './assets/githubBW.svg'
import GithubC from './assets/githubColor.svg'
import KaggleBW from './assets/KaggleBW.png'
import KaggleC from './assets/KaggleColor.svg'
import LinkBW from './assets/linkedinBw.svg'
import LinkC from './assets/linkedinColor.svg'

export default function Contato(){
    return(
        <div className="container-contato">
            <div className="contatos">
                <div className="imagem-contatos">
                    <img src={FaceBW} id="facebw"></img>
                    <span id="face"><img src={FaceC}></img></span>
                </div>                
                <div className="imagem-contatos">
                    <img src={GithubBW} id="gitbw"></img>
                    <span id="git"><img src={GithubC}></img></span>
                </div> 
                <div className="imagem-contatos">
                    <img src={KaggleBW} id="kagbw"></img>
                    <span id="kag"><img src={KaggleC}></img></span>
                </div> 
                <div className="imagem-contatos">
                    <img src={LinkBW} id="linkbw"></img>
                    <span id="link"><img src={LinkC}></img></span>
                </div> 
            </div>
            <footer>

            </footer>
        </div>
    )
}